import { mapState, mapMutations } from 'vuex';
export default {
	computed: {
		...mapState(['hasLogin', 'userId'])
	},
		data(){
			return{
				nickName:'',
			}
		},
		methods: {
			
			pass(e){
				let that = this;
				console.log(that.userId);
				uni.request({
					url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/update/user/nickName',
					method: 'PUT',
					data: {
						id:that.userId,
						nickName:that.nickName
					},
					success: res => {
						console.log(res);
						uni.showToast({
							title:'修改成功'
						});
					},
					fail: () => {},
					complete: () => {}
				});
			},
		}
		
	}
